import { Component, ViewChild, OnDestroy, AfterViewInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { NotifierService } from 'angular-notifier';

import { ERRORS } from '@api/index';

import { TokenService } from '@shared/token/token.service';
import { ICountryCode, ISmsToken } from '@shared/models';

import { AuthService } from '@auth/auth.service';

import {
  ILoginForm,
  LoginPhoneComponent,
} from './login-phone/login-phone.component';

enum LOGIN_STEPS {
  PHONE = 'phone',
  SMS = 'sms',
  LOADING = 'loading',
}

@Component({
  selector: 'j-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnDestroy {
  isLoading = false;

  steps = LOGIN_STEPS;
  currentStep: LOGIN_STEPS = this.steps.PHONE;

  phoneError = '';
  loginForm: ILoginForm;

  phone: string;

  @ViewChild('loginPhone') loginPhone: LoginPhoneComponent;

  readonly subscription = new Subscription();
  countryCode: ICountryCode;

  constructor(
    private auth: AuthService,
    private tokenService: TokenService,
    private route: ActivatedRoute,
    private router: Router,
    private notifierService: NotifierService
  ) {
    this.subscription.add(
      this.route.paramMap
        .pipe(map(() => window.history.state))
        .subscribe((state) => {
          if (state.phone) {
            this.phone = state.phone;
          }
          if (state.error) {
            this.phoneError = state.error;
          }
        })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onSmsClose() {
    this.navigateToPhone();
    this.phoneError = '';
  }

  onPhoneSubmit(loginForm: ILoginForm) {
    this.loginForm = loginForm;
    this.phone = '+' + this.loginForm.phone;
    this.countryCode = this.loginForm.countryCode;

    try {
      const deviceToken = this.tokenService.getDeviceToken(
        this.loginForm.password
      );
      localStorage.setItem('countryCode', JSON.stringify(this.countryCode));
      localStorage.setItem('phone', this.loginForm.phone);
      
      if (deviceToken) {
        this.login();
      } else {
        this.navigateToSms();
      }
    } catch (error) {
      this.navigateToSms();
    }
  }

  async onSmsSubmit(smsToken: ISmsToken) {
    this.auth.setSmsToken(smsToken);
    this.currentStep = this.steps.LOADING;
    try {
      const deviceToken = await this.auth.requestDevice(
        this.phone,
        smsToken.type
      );
      this.tokenService.setDeviceToken(
        deviceToken.signature,
        this.loginForm.password
      );
      this.login();
    } catch (error) {
      this.navigateToPhone();

      switch (error.code) {
        case ERRORS.DEVICE_TOKEN_INVALID:
        case ERRORS.RESOURCE_NOT_FOUND:
        case ERRORS.AUTHORIZATION_ERROR:
        case ERRORS.INVALID_GRANT:
          this.phoneError = 'ERRORS.WRONG_CREDENTIALS';
          break;
        case ERRORS.USER_BLOCKED:
        case ERRORS.RESOURCE_BLOCKED:
          this.phoneError = 'ERRORS.ACCOUNT_BLOCKED';
          break;
        default:
          this.phoneError = error.description;
          break;
      }
    }
  }

  async login() {
    this.isLoading = true;
    this.phoneError = '';

    try {
      await this.auth.login(
        '+' + this.loginForm.phone,
        this.loginForm.password
      );
      this.router.navigateByUrl('/');
      this.notifierService.hideAll();
    } catch (error) {
      this.navigateToPhone();

      if (!error.status) {
        this.phoneError = 'ERRORS.API_UNAVAILABLE';
      } else {
        switch (error.code) {
          case ERRORS.RESOURCE_NOT_FOUND:
          case ERRORS.AUTHORIZATION_ERROR:
          case ERRORS.INVALID_GRANT:
            this.phoneError = 'ERRORS.WRONG_CREDENTIALS';
            break;

          case ERRORS.RESOURCE_BLOCKED:
            this.tokenService.clearDeviceToken();
            this.phoneError = 'ERRORS.ACCOUNT_BLOCKED';
            break;

          case ERRORS.DEVICE_NOT_AUTHORIZED:
          case ERRORS.DEVICE_TOKEN_INVALID:
          case ERRORS.TOKEN_EXPIRED:
            this.navigateToSms();
            // this.phoneError = error.description;
            break;

          default:
            this.phoneError = error.description;
            break;
        }
      }

      this.loginPhone.triggerErrorAnimation();
      this.isLoading = false;
    }
  }

  private navigateToSms() {
    this.currentStep = LOGIN_STEPS.SMS;
  }

  private navigateToPhone() {
    this.currentStep = LOGIN_STEPS.PHONE;
  }
}
