import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { AUTH_TYPE } from '@auth/auth.model';

@Component({
  selector: 'j-register-error',
  templateUrl: './register-error.component.html',
  styleUrls: ['./register-error.component.scss'],
})
export class RegisterErrorComponent implements OnDestroy {
  authType: AUTH_TYPE;
  error: any;
  iin: string;
  subscription = new Subscription();

  constructor(private route: ActivatedRoute) {
    const routeDataSubscription = this.route.data.subscribe((data) => {
      this.authType = data.action;
    });

    const routeStateSubscription = this.route.paramMap
      .pipe(map(() => window.history.state))
      .subscribe((state) => {
        this.error = state.error;
        this.iin = state.iin;
      });

    this.subscription.add(routeStateSubscription);
    this.subscription.add(routeDataSubscription);
  }

  get description() {
    return this.authType === AUTH_TYPE.REGISTER
      ? 'AUTH.ERROR.DESCRIPTION_REGISTER'
      : 'AUTH.ERROR.DESCRIPTION_RESTORE';
  }

  get state() {
    return {
      iin: this.iin,
    };
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
