import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { map } from 'rxjs/operators';
import { Subscription } from 'rxjs';

import { ICountryCode, ISmsToken } from '@shared/models';

import { AuthService } from '@auth/auth.service';
import { AUTH_TYPE, IIN_AND_PHONE_STATUS } from '@auth/auth.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CanRegisterComponent, CanRestoreComponent } from '@auth/modals';
import { Dictionary } from 'lodash';
import { SMS_HEADERS } from '@api/api.service';
import { NavService } from '@shared/nav/nav.service';

const MODAL_OPTIONS = {
  backdropClass: 'backdrop_light',
  windowClass: 'modal_light modal-auth',
  size: 'md',
  centered: true,
};

@Component({
  selector: 'j-register-sms',
  templateUrl: './register-sms.component.html',
  styleUrls: ['./register-sms.component.scss'],
})
export class RegisterSmsComponent implements OnDestroy {
  phone: string;
  iin: string;
  countryCode: ICountryCode;
  isFormLoaded = false;
  authType: AUTH_TYPE;

  params: Dictionary<any>;
  headerParams: Dictionary<any>;

  readonly subs = new Subscription();

  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router,
    private modalService: NgbModal,
    private navService: NavService
  ) {
    this.navService.triggerActiveItem(route.snapshot.url[0].path);
    this.subs.add(
      this.route.paramMap
        .pipe(map(() => window.history.state))
        .subscribe((state) => {
          this.phone = state.phone;
          this.countryCode = state.countryCode;
          this.iin = state.iin;
          this.params = {
            actionIds: [this.iin],
          };
        })
    );

    const routeSubscription = this.route.data.subscribe((data) => {
      this.authType = data.action;
      this.headerParams = {
        smsHeader: SMS_HEADERS[this.authType.toUpperCase()]
      };
    });

    this.subs.add(routeSubscription);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  onSmsFail() {
    this.navigateToPhone();
  }

  onFormLoaded() {
    this.isFormLoaded = true;
  }

  async onSmsSubmit(smsToken: ISmsToken) {
    this.authService.setSmsToken(smsToken);
    this.checkIinAndPhone();
  }

  navigateToPhone(event: Event = null) {
    event?.preventDefault();
    this.router.navigate([`phone`], {
      relativeTo: this.route.parent,
      state: { iin: this.iin, phone: this.phone, countryCode: this.countryCode },
    });
  }

  private async checkIinAndPhone() {
    try {
      const iinAndPhoneCheckResult = await this.authService.checkIinAndPhone(
        this.iin,
        this.phone
      );

      this.handleIinAndPhoneCheck(iinAndPhoneCheckResult.status);
    } catch (error) {
      this.navigateToError(error);
    }
  }

  private handleIinAndPhoneCheck(status: IIN_AND_PHONE_STATUS) {
    switch (status) {
      case IIN_AND_PHONE_STATUS.BLOCKED:
        this.navigateToUserBlocked();
        break;
      case IIN_AND_PHONE_STATUS.DOES_NOT_EXIST:
        this.navigateToNoAccount();
        break;
      case IIN_AND_PHONE_STATUS.INVALID_DATA:
      case IIN_AND_PHONE_STATUS.INCORRECT_DATA:
        this.navigateToNoEmail();
        break;
      case IIN_AND_PHONE_STATUS.DATA_MISMATCH:
        this.navigateToPhoneNotMatch();
        break;
      case IIN_AND_PHONE_STATUS.ALREADY_REGISTERED:
        this.handleAlreadyRegistered();
        break;
      case IIN_AND_PHONE_STATUS.CAN_REGISTER:
        this.handleCanRegister();
        break;
    }
  }

  private navigateToError(error) {
    this.router.navigate([`error`], {
      relativeTo: this.route.parent,
      state: {
        error,
        iin: this.iin,
      },
    });
  }

  private navigateToUserBlocked() {
    this.router.navigate([`user-blocked`], {
      relativeTo: this.route.parent,
    });
  }

  private navigateToEmail(prefix: string = '') {
    this.router.navigate([`${prefix}email`], {
      relativeTo: this.route.parent,
      state: { phone: this.phone, iin: this.iin },
    });
  }

  private navigateToNoAccount() {
    this.router.navigate([`no-account`], {
      relativeTo: this.route.parent,
    });
  }

  private navigateToNoEmail() {
    this.router.navigate([`no-email`], {
      relativeTo: this.route.parent,
    });
  }

  private navigateToPhoneNotMatch() {
    this.router.navigate([`phone-not-match`], {
      relativeTo: this.route.parent,
      state: { iin: this.iin, phone: this.phone, countryCode: this.countryCode },
    });
  }

  private handleAlreadyRegistered() {
    switch (this.authType) {
      case AUTH_TYPE.RESTORE:
        this.navigateToEmail();
        break;
      case AUTH_TYPE.REGISTER:
        this.showCanRestoreModal();
        break;
    }
  }

  private async showCanRegisterModal() {
    const modalRef = this.modalService.open(
      CanRegisterComponent,
      MODAL_OPTIONS
    );
    try {
      await modalRef.result;
      this.navigateToRegisterEmail();
    } catch (error) {
      this.navigateToPhone();
    }
  }

  private async showCanRestoreModal() {
    const modalRef = this.modalService.open(CanRestoreComponent, MODAL_OPTIONS);
    try {
      const closeAction = await modalRef.result;
      if (closeAction === 'restore') {
        this.navigateToRestoreEmail();
      } else if (closeAction === 'login') {
        this.navigateToLogin();
      }
    } catch (error) {
      this.navigateToPhone();
    }
  }

  private handleCanRegister() {
    switch (this.authType) {
      case AUTH_TYPE.REGISTER:
        this.navigateToEmail();
        break;
      case AUTH_TYPE.RESTORE:
        this.showCanRegisterModal();
        break;
    }
  }

  private navigateToRegisterEmail() {
    this.navigateToEmail('../register/');
  }

  private navigateToRestoreEmail() {
    this.navigateToEmail('../restore/');
  }

  private navigateToLogin() {
    this.router.navigate([`../login`], {
      relativeTo: this.route.parent,
    });
  }
}
