import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Login
import { LoginComponent } from './login/login.component';
import { LoginSmsComponent } from './login/login-sms/login-sms.component';
import { LoginPhoneComponent } from './login/login-phone/login-phone.component';

// Register
import { RegisterComponent } from './register/register.component';
import { RegisterIinComponent } from './register/register-iin/register-iin.component';
import { RegisterPhoneComponent } from './register/register-phone/register-phone.component';
import { RegisterSmsComponent } from './register/register-sms/register-sms.component';
import { RegisterEmailComponent } from './register/register-email/register-email.component';
import { RegisterPasswordComponent } from './register/register-password/register-password.component';
import { RegisterResultComponent } from './register/register-result/register-result.component';

// Errors
import { NoAccountComponent } from './errors/no-account/no-account.component';
import { NoEmailComponent } from './errors/no-email/no-email.component';
import { UserBlockedComponent } from './errors/user-blocked/user-blocked.component';
import { PhoneNotMatchComponent } from './errors/phone-not-match/phone-not-match.component';

import { AUTH_TYPE } from './auth.model';
import { RegisterErrorComponent } from './register/register-error/register-error.component';

export const routeComponents = [
  // Login
  LoginComponent,
  LoginPhoneComponent,
  LoginSmsComponent,

  // Register
  RegisterComponent,
  RegisterPhoneComponent,
  RegisterSmsComponent,
  RegisterEmailComponent,
  RegisterPasswordComponent,
  RegisterResultComponent,

  // Errors
  NoAccountComponent,
  NoEmailComponent,
  UserBlockedComponent,
  PhoneNotMatchComponent,
];

const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full',
  },
  {
    path: 'login',
    component: LoginComponent,
  },
  {
    path: 'register',
    component: RegisterComponent,
    children: [
      {
        path: '',
        redirectTo: 'iin',
      },
      {
        path: 'iin',
        component: RegisterIinComponent,
        data: {
          title: 'AUTH.REGISTER_TITLE',
          action: AUTH_TYPE.REGISTER,
        },
      },
      {
        path: 'no-account',
        component: NoAccountComponent,
      },
      {
        path: 'no-email',
        component: NoEmailComponent,
      },
      {
        path: 'user-blocked',
        component: UserBlockedComponent,
      },
      {
        path: 'phone',
        component: RegisterPhoneComponent,
        data: {
          title: 'SHARED.PHONE',
          action: AUTH_TYPE.REGISTER,
        },
      },
      {
        path: 'sms',
        component: RegisterSmsComponent,
        data: {
          action: AUTH_TYPE.REGISTER,
        },
      },
      {
        path: 'phone-not-match',
        component: PhoneNotMatchComponent,
      },
      {
        path: 'email',
        component: RegisterEmailComponent,
      },
      {
        path: 'password',
        component: RegisterPasswordComponent,
        data: {
          action: AUTH_TYPE.REGISTER,
        },
      },
      {
        path: 'error',
        component: RegisterErrorComponent,
        data: {
          action: AUTH_TYPE.REGISTER,
        },
      },
      {
        path: 'result',
        component: RegisterResultComponent,
        data: {
          action: AUTH_TYPE.REGISTER,
        },
      },
    ],
  },
  {
    path: 'restore',
    component: RegisterComponent,
    children: [
      {
        path: '',
        redirectTo: 'iin',
      },
      {
        path: 'iin',
        component: RegisterIinComponent,
        data: {
          title: 'AUTH.RESTORE',
          action: AUTH_TYPE.RESTORE,
        },
      },
      {
        path: 'no-account',
        component: NoAccountComponent,
      },
      {
        path: 'no-email',
        component: NoEmailComponent,
      },
      {
        path: 'user-blocked',
        component: UserBlockedComponent,
      },
      {
        path: 'phone',
        component: RegisterPhoneComponent,
        data: {
          title: 'SHARED.PHONE',
          action: AUTH_TYPE.RESTORE,
        },
      },
      {
        path: 'sms',
        component: RegisterSmsComponent,
        data: {
          action: AUTH_TYPE.RESTORE,
        },
      },
      {
        path: 'phone-not-match',
        component: PhoneNotMatchComponent,
      },
      {
        path: 'email',
        component: RegisterEmailComponent,
      },
      {
        path: 'password',
        component: RegisterPasswordComponent,
        data: {
          action: AUTH_TYPE.RESTORE,
        },
      },
      {
        path: 'error',
        component: RegisterErrorComponent,
        data: {
          action: AUTH_TYPE.RESTORE,
        },
      },
      {
        path: 'result',
        component: RegisterResultComponent,
        data: {
          action: AUTH_TYPE.RESTORE,
        },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AuthRoutingModule {}
