import { Component, ViewChild, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { ERRORS } from '@api/index';

import { SmsInputComponent } from '@shared/input/sms-input/sms-input.component';

import { AUTH_CONFIG } from '@auth/auth.config';
import { AuthService } from '@auth/auth.service';
import { ContactModalComponent } from '@auth/modals/contact-modal/contact-modal.component';
import { ExpiredComponent } from '@auth/modals/expired/expired.component';
import {
  SMS_CODE_LENGTH,
  SMS_TIMER_COUNTDOWN,
  SMS_ERROR_TYPES,
  SMS_TABS,
} from '@shared/models';
import { NavService } from '@shared/nav/nav.service';

@Component({
  selector: 'j-register-email',
  templateUrl: './register-email.component.html',
  styleUrls: ['./register-email.component.scss'],
})
export class RegisterEmailComponent implements OnDestroy {
  phone: string;
  iin: string;
  email: string;

  emailCodeForm: FormGroup;

  smsCodeLength = SMS_CODE_LENGTH;

  errorText: string;
  errorParams: {
    triesLeft: number;
  };

  hasError: boolean;

  timer: any;
  timerValue: number;
  timerMaxValue = SMS_TIMER_COUNTDOWN;
  isTimerRunning: boolean;

  blockedTimer: any;
  blockedTimerValue: number;
  isBlocked: boolean;
  isBlockedTimerRunning: boolean;

  expireTimer: any;
  expireTimerValue: number;
  expireTimerMaxValue = AUTH_CONFIG.EMAIL_COUNTDOWN;
  isExpireTimerRunning: boolean;

  errorTypes = SMS_ERROR_TYPES;

  isLoading: boolean;
  isReady = false;
  isSubmitted: boolean;

  currentTab = SMS_TABS.FORM;
  authTabs = SMS_TABS;

  @ViewChild('codeInput') codeInput: SmsInputComponent;

  readonly subs = new Subscription();

  constructor(
    private route: ActivatedRoute,
    private auth: AuthService,
    private router: Router,
    private modalService: NgbModal,
    private navService: NavService
  ) {
    this.navService.triggerActiveItem(route.snapshot.url[0].path);
    this.emailCodeForm = new FormGroup({
      emailCode: new FormControl('', Validators.required),
    });

    const routeSubscription = this.route.paramMap
      .pipe(map(() => window.history.state))
      .subscribe((state) => {
        this.phone = state.phone;
        this.iin = state.iin;
        this.requestEmail();
      });

    const formSubscription = this.emailCodeForm.valueChanges.subscribe(
      (value) => {
        this.isSubmitted = false;
        if (value.emailCode && value.emailCode.length === this.smsCodeLength) {
          this.onSubmit();
        }
      }
    );

    this.subs.add(routeSubscription);
    this.subs.add(formSubscription);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  openContactModal() {
    this.modalService.open(ContactModalComponent, {
      centered: true,
    });
  }

  setTab(tab: SMS_TABS) {
    this.currentTab = tab;
  }

  get f() {
    return this.emailCodeForm.controls;
  }

  async requestEmail() {
    this.f.emailCode.setValue('');
    this.errorText = '';
    this.hasError = false;
    try {
      const emailToken = await this.auth.requestEmail(this.phone, this.iin);

      this.setTab(this.authTabs.FORM);
      this.email = emailToken.email.replace(/\*/gm, '');
      this.codeInput.focus();
    } catch (error) {
      switch (error.code) {
        case ERRORS.ARGUMENT_MISSING:
          this.router.navigate([`phone`], {
            relativeTo: this.route.parent,
          });
          break;
        case ERRORS.RESOURCE_BLOCKED:
          if (error.data && error.data.millisecondsLeft) {
            this.startBlockedTimer(error.data.millisecondsLeft);
          }
          this.setTab(this.authTabs.FORBIDDEN_BLOCKED);
          break;
        default:
          this.setTab(this.authTabs.ERRORED);
          break;
      }
    } finally {
      this.isReady = true;
    }
  }

  navigateToPhone(event: Event = null) {
    event?.preventDefault();
    this.router.navigate([`phone`], {
      relativeTo: this.route.parent,
      state: {
        iin: this.iin,
        phone: this.phone,
      },
    });
  }

  async onSubmit() {
    this.isSubmitted = true;

    if (this.emailCodeForm.valid) {
      this.isLoading = true;
      const emailCode = this.f.emailCode.value;

      try {
        await this.auth.submitEmail(emailCode, this.phone, this.iin);

        this.router.navigate([`password`], {
          relativeTo: this.route.parent,
          state: { phone: this.phone, iin: this.iin },
        });
      } catch (error) {
        this.hasError = true;

        switch (error.code) {
          case ERRORS.ARGUMENT_MISSING:
            this.router.navigate([`phone`], {
              relativeTo: this.route.parent,
            });
            break;
          case ERRORS.RESOURCE_BLOCKED:
          case ERRORS.INVALID_CODE:
            let triesLeft;
            if (error.data && error.data.triesLeft) {
              triesLeft = error.data.triesLeft;
              this.errorParams = { triesLeft };
              this.errorText = 'ERRORS.INVALID_CODE.';
              switch (triesLeft % 20) {
                case 1:
                  this.errorText += `ONE`;
                  break;
                case 2:
                case 3:
                case 4:
                  this.errorText += `FEW`;
                  break;
                default:
                  this.errorText += `OTHER`;
              }
            } else {
              this.startTimer();
              this.setTab(this.authTabs.FORBIDDEN);
            }
            break;

          case ERRORS.FORBIDDEN:
            this.startTimer();
            this.setTab(this.authTabs.FORBIDDEN);
            break;

          case ERRORS.PHONE_TOKEN_EXPIRED:
          case ERRORS.TOKEN_EXPIRED:
            const modalRef = this.modalService.open(ExpiredComponent, {
              centered: true,
            });
            try {
              await modalRef.result;
            } catch (error) {
            } finally {
              this.router.navigate([`iin`], {
                relativeTo: this.route.parent,
                state: { iin: this.iin },
              });
            }
            break;

          default:
            this.errorText = error.description;
            break;
        }
        this.codeInput.triggerHelperAnimation();
      } finally {
        this.isLoading = false;
      }
    }
  }

  private startTimer(timerMaxValue = this.timerMaxValue) {
    this.isTimerRunning = true;
    this.timerValue = timerMaxValue;
    this.timer = setInterval(() => {
      this.timerValue--;

      if (this.timerValue === 0) {
        this.stopTimer();
      }
    }, 1000);
  }

  private stopTimer() {
    clearInterval(this.timer);
    this.isTimerRunning = false;
  }

  private startBlockedTimer(millisecondsLeft: number) {
    this.blockedTimerValue = Math.ceil(millisecondsLeft / 1000);
    this.isBlockedTimerRunning = true;

    this.blockedTimer = setInterval(() => {
      this.blockedTimerValue--;

      if (this.blockedTimerValue === 0) {
        this.stopBlockedTimer();
      }
    }, 1000);
  }

  private stopBlockedTimer() {
    clearInterval(this.blockedTimer);
    this.isBlockedTimerRunning = false;
    this.blockedTimerValue = null;
  }
}
