import {
  Component,
  ViewChild,
  OnDestroy,
  Output,
  EventEmitter,
  AfterViewInit,
} from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { Subscription } from 'rxjs';

import { IinInputComponent } from '@shared/input/iin-input/iin-input.component';
import { validateIin } from '@shared/validators/iin';

import { AuthService } from '@auth/auth.service';
import { IIN_STATUS } from '@auth/auth.model';

export interface IAuthIinResult {
  iin: string;
  status?: IIN_STATUS;
}

@Component({
  selector: 'j-auth-iin',
  templateUrl: './auth-iin.component.html',
  styleUrls: ['./auth-iin.component.scss'],
})
export class AuthIinComponent implements OnDestroy, AfterViewInit {
  IIN = '';

  isLoading: boolean;
  isSubmitted: boolean;

  openIinForm: FormGroup;

  hasError: boolean;
  errorText: string;

  name: string;

  @ViewChild('iinInput') iinInput: IinInputComponent;

  @Output() submitted = new EventEmitter<IAuthIinResult>();

  readonly subs = new Subscription();

  constructor(private authService: AuthService) {
    this.openIinForm = new FormGroup({
      iin: new FormControl(this.IIN, [Validators.required, validateIin]),
    });

    const formSubscription = this.openIinForm.valueChanges.subscribe(
      (value) => {
        this.isSubmitted = false;
        this.errorText = '';
      }
    );

    this.subs.add(formSubscription);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  ngAfterViewInit() {
    this.iinInput.focus();
  }

  async onSubmit() {
    this.isSubmitted = true;

    if (!this.openIinForm.valid) {
      this.validateIin(this.f.iin.errors);
      return;
    }

    this.isLoading = true;

    this.submitted.emit({
      iin: this.iin,
      // status: result.status,
    });

    // try {
    //   const result = await this.authService.checkIin(this.iin);

    //   this.submitted.emit({
    //     iin: this.iin,
    //     status: result.status,
    //   });
    // } catch (error) {
    //   switch (error.code) {
    //     case ERRORS.INVALID_DATA:
    //       this.submitted.emit({
    //         iin: this.iin,
    //         status: IIN_STATUS.INVALID_DATA,
    //       });
    //       break;
    //   }
    //   this.iinInput.triggerHelperAnimation();
    // } finally {
    //   this.isLoading = false;
    // }
  }

  get f() {
    return this.openIinForm.controls;
  }

  get iin() {
    return this.f.iin.value;
  }

  private validateIin(errors) {
    if (!errors || !errors.iin) {
      return;
    }
    if (errors.iin.length) {
      this.errorText = `ERRORS.IIN_LENGTH`;
    } else if (errors.iin.date) {
      this.errorText = `ERRORS.IIN_DATE`;
    }
  }
}
