import { Component } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { ERRORS } from '@api/index';

import { AuthService } from '@auth/auth.service';
import { ExpiredComponent } from '@auth/modals';
import { AUTH_TYPE } from '@auth/auth.model';
import { INewPasswordResult } from '@shared/components/new-password/new-password.component';
import { NavService } from '@shared/nav/nav.service';

@Component({
  selector: 'j-register-password',
  templateUrl: './register-password.component.html',
  styleUrls: ['./register-password.component.scss'],
})
export class RegisterPasswordComponent {
  authType: AUTH_TYPE;

  phone: string;
  iin: string;

  errorText: string;

  isLoading = false;

  readonly subs = new Subscription();

  constructor(
    private auth: AuthService,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private navService: NavService
  ) {
    this.navService.triggerActiveItem(route.snapshot.url[0].path);
    const routeStateSubscription = this.route.paramMap
      .pipe(map(() => window.history.state))
      .subscribe((state) => {
        this.phone = state.phone;
        this.iin = state.iin;

        if (!this.iin) {
          this.navigateToIin();
        } else if (!this.phone) {
          this.navigateToPhone();
        }
      });

    const routeDataSubscription = this.route.data.subscribe((data) => {
      this.authType = data.action;
    });

    this.subs.add(routeStateSubscription);
    this.subs.add(routeDataSubscription);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  async onSubmit(newPasswordResult: INewPasswordResult) {
    this.isLoading = true;

    try {
      if (this.authType == AUTH_TYPE.RESTORE) {
        await this.auth.restore(
          this.phone,
          newPasswordResult.newPassword,
          this.iin
        );
      } else {
        await this.auth.register(
          this.phone,
          newPasswordResult.newPassword,
          this.iin
        );
      }

      this.navigateToResult(newPasswordResult.newPassword);
    } catch (error) {
      this.mapError(error);
    } finally {
      this.isLoading = false;
    }
  }

  private async mapError(error) {
    switch (error.code) {
      case ERRORS.RESOURCE_NOT_FOUND:
      case ERRORS.FORBIDDEN:
      case ERRORS.RESOURCE_EXISTS:
        this.navigateToPhoneNotMatch();
        break;
      case ERRORS.PHONE_TOKEN_EXPIRED:
      case ERRORS.TOKEN_EXPIRED:
        this.showExpiredModal();
        break;
      case ERRORS.INVALID_PHONE_FORMAT:
      case ERRORS.ARGUMENT_MISSING:
        this.navigateToPhone();
        break;
      default:
        this.errorText = error.description;
        break;
    }
  }

  private async showExpiredModal() {
    const modalRef = this.modalService.open(ExpiredComponent, {
      centered: true,
    });
    try {
      await modalRef.result;
    } catch (error) {
    } finally {
      this.navigateToIin();
    }
  }

  private navigateToIin() {
    this.router.navigate([`iin`], {
      relativeTo: this.route.parent,
      state: { iin: this.iin || '' },
    });
  }

  private navigateToPhone() {
    this.router.navigate([`phone`], {
      relativeTo: this.route.parent,
      state: { iin: this.iin },
    });
  }

  private navigateToResult(newPassword: string) {
    this.router.navigate([`result`], {
      relativeTo: this.route.parent,
      state: { phone: this.phone, password: newPassword },
    });
  }

  private navigateToPhoneNotMatch() {
    this.router.navigate([`phone-not-match`], {
      relativeTo: this.route.parent,
    });
  }
}
