import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SMS_HEADERS } from '@api/api.service';
import { ICountryCode, ISmsToken, TOKEN_TYPE } from '@shared/models';
import { CONFIRM_SIGNATURE_TYPE } from '@shared/components';
import { Dictionary } from 'lodash';

@Component({
  selector: 'j-login-sms',
  templateUrl: './login-sms.component.html',
  styleUrls: ['./login-sms.component.scss'],
})
export class LoginSmsComponent {
  @Input()
  isLoading: boolean;

  @Input()
  phone: string;

  @Input()
  countryCode: ICountryCode;

  isReady = false;
  headerParams: Dictionary<any>;

  preferredSignatureType = localStorage.getItem('preferredSignatureType');
  tokenType = TOKEN_TYPE;

  @Output() submitted = new EventEmitter<ISmsToken>();
  @Output() closed = new EventEmitter();

  constructor() {
    this.headerParams = {
      smsHeader: SMS_HEADERS.AUTHORIZATION
    };
    this.preferredSignatureType = localStorage.getItem('preferredSignatureType') || CONFIRM_SIGNATURE_TYPE.SMS;
  }

  onSmsLoaded() {
    this.isReady = true;
  }

  onSubmit(smsToken: ISmsToken) {
    this.submitted.emit(smsToken);
  }

  navigateToPhone(event: Event = null) {
    event?.preventDefault();
    this.closed.emit();
  }

  changeCodeRequestMethod(method) {
    localStorage.setItem(
      'preferredSignatureType',
      method === TOKEN_TYPE.OTP
        ? CONFIRM_SIGNATURE_TYPE.OTP
        : CONFIRM_SIGNATURE_TYPE.SMS
    );
    this.preferredSignatureType = method;
  }
}
