import { Component, ViewChild, HostListener, OnDestroy, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';

import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { ERRORS } from '@api/index';

import { MaskedService } from '@shared/masked/masked.service';
import { PhoneInputComponent } from '@shared/input/phone-input/phone-input.component';
import { NavService } from '@shared/nav/nav.service';

import { AUTH_TYPE, DEFAULT_COUNTRY, DEFAULT_COUNTRY_CODE } from '@auth/auth.model';
import { AuthService } from '@auth/auth.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { InvalidPhoneComponent } from '@auth/modals/invalid-phone/invalid-phone.component';
import { MaskPipe } from 'ngx-mask';
import { TranslateService } from '@ngx-translate/core';
import { ICountryCode, ICountryCodeResponseObj } from '@shared/models';
import { DictionariesService } from '@shared/services';

@Component({
  selector: 'j-register-phone',
  templateUrl: './register-phone.component.html',
  styleUrls: ['./register-phone.component.scss'],
})
export class RegisterPhoneComponent implements OnDestroy, OnInit {
  PHONE = '';

  isLoading: boolean;
  isSubmitted: boolean;

  hasNoAccount = false;

  title: string;
  authType: AUTH_TYPE;

  errorText: string;

  registerPhoneForm: FormGroup;

  phone: string;
  iin: string;

  countryCodes: ICountryCode[];
  countryCode = {
    code: '+7',
    country: this.translateService.instant(
      `COUNTRIES.${DEFAULT_COUNTRY.KAZAKHSTAN}`
    ),
    id: DEFAULT_COUNTRY.KAZAKHSTAN,
    countryCode: DEFAULT_COUNTRY_CODE.KAZAKHSTAN,
  };

  @ViewChild('phoneInput') phoneInput: PhoneInputComponent;

  readonly subs = new Subscription();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private maskedService: MaskedService,
    private maskPipe: MaskPipe,
    private navService: NavService,
    private authService: AuthService,
    private modalService: NgbModal,
    private translateService: TranslateService,
    private dictionariesService: DictionariesService,
  ) {
    this.navService.triggerActiveItem(route.snapshot.url[0].path);
    this.registerPhoneForm = new FormGroup({
      phone: new FormControl(this.PHONE, Validators.required),
    });

    this.navService.triggerRegisterError('phone', false);
    this.navService.triggerRegisterError('no-account', false);
    this.navService.triggerRegisterError('no-email', false);
    this.navService.triggerRegisterError('user-blocked', false);
    this.navService.triggerRegisterError('phone-not-match', false);

    const formSubscription = this.registerPhoneForm.valueChanges.subscribe(
      () => {
        this.isSubmitted = false;
      }
    );

    const routeDataSubscription = this.route.data.subscribe((data) => {
      this.title = data.title;
      this.authType = data.action;
    });

    const routeStateSubscription = this.route.paramMap
      .pipe(map(() => window.history.state))
      .subscribe((state) => {
        if (state.iin) {
          this.iin = state.iin;
        } else {
          this.navigateToIin();
        }
        if (state.countryCode) {
          this.countryCode = state.countryCode;
          if (state.phone) {
            this.f.phone.setValue(state.phone);
          }
        }
      });

    this.subs.add(formSubscription);
    this.subs.add(routeDataSubscription);
    this.subs.add(routeStateSubscription);
  }

  ngOnInit() {
    this.fetchCountryCodes();
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  @HostListener('keyup.enter')
  onEnter() {
    this.phoneInput.blur();
    this.onSubmit();
  }

  focus() {
    this.phoneInput.focus();
  }

  navigateFromModal(path: string) {
    this.router.navigate([`/auth/${path}/sms`], {
      state: { phone: this.phone, iin: this.iin },
    });
  }

  async fetchCountryCodes() {
    try {
      await this.dictionariesService.fetchCountryCodesDictionary().then(response => {
        this.countryCodes = response.map((countryObj: ICountryCodeResponseObj) => ({
          id: countryObj.id,
          code: countryObj.value.description,
          country: countryObj.value.name,
          countryCode: countryObj.value.code,
        }));
      });
    } catch (err) {}
  }

  async onSubmit() {
    this.isSubmitted = true;
    if (!this.registerPhoneForm.valid) {
      this.errorText = 'ERRORS.NOT_ENOUGH_SYMBOLS';
      return;
    }

    this.isLoading = true;

    try {
      this.phone = '+' + this.maskedService.getUnmaskedPhone(this.f.phone.value);
      await this.authService.checkPhone(this.phone);
      this.navigateToSms();
    } catch (error) {
      switch (error.code) {
        case ERRORS.INVALID_PHONE_FORMAT:
          await this.showInvalidPhoneFormatModal();
          break;
        default:
          this.errorText = error.description;
          break;
      }
    } finally {
      this.isLoading = false;
    }
  }

  get f() {
    return this.registerPhoneForm.controls;
  }

  private async showInvalidPhoneFormatModal() {
    const modalRef = this.modalService.open(InvalidPhoneComponent, {
      centered: true,
    });

    try {
      await modalRef.result;
    } catch (error) {
    } finally {
      this.phoneInput.focus();
    }
  }

  private navigateToSms() {
    this.router.navigate([`sms`], {
      relativeTo: this.route.parent,
      state: {
        phone: this.phone,
        iin: this.iin,
        countryCode: this.countryCode,
      },
    });
  }

  private navigateToIin() {
    this.router.navigate([`iin`], {
      relativeTo: this.route.parent,
    });
  }

  onCountrySelected(countryCode) {
    this.countryCode = countryCode;
  }
}
