import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NavService } from '@shared/nav/nav.service';
import { ICountryCode } from '@shared/models';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'j-phone-not-match',
  templateUrl: './phone-not-match.component.html',
  styleUrls: ['./phone-not-match.component.scss'],
})
export class PhoneNotMatchComponent implements OnDestroy {
  iin: string;
  phone: string;
  countryCode: ICountryCode;

  readonly subscription = new Subscription();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private navService: NavService
  ) {
    this.navService.triggerRegisterError('phone', true);
    this.navService.triggerRegisterError('phone-not-match', true);

    this.subscription.add(
      this.route.paramMap
        .pipe(map(() => window.history.state))
        .subscribe((state) => {
          this.phone = state.phone;
          this.iin = state.iin;
          this.countryCode = state.countryCode;
        })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  navigateToPhone(event: Event = null) {
    event?.preventDefault();
    this.router.navigate([`phone`], {
      relativeTo: this.route.parent,
      state: {
        iin: this.iin,
        phone: this.phone,
        countryCode: this.countryCode,
      },
    });
  }
}
