import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { AuthService } from '@auth/auth.service';
import { AUTH_TYPE } from '@auth/auth.model';
import { TokenService } from '@shared/token/token.service';
import { HEADERS } from '@api/api.service';
import { TOKEN_TYPE } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';

@Component({
  selector: 'j-register-result',
  templateUrl: './register-result.component.html',
  styleUrls: ['./register-result.component.scss'],
})
export class RegisterResultComponent implements OnDestroy {
  isLoading = false;

  authType: AUTH_TYPE;
  authTypes = AUTH_TYPE;

  phone: string;
  password: string;

  readonly subs = new Subscription();

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private auth: AuthService,
    private tokenService: TokenService,
    private navService: NavService
  ) {
    this.navService.triggerActiveItem(route.snapshot.url[0].path);
    const routeStateSubscription = this.route.paramMap
      .pipe(map(() => window.history.state))
      .subscribe((state) => {
        this.phone = state.phone;
        this.password = state.password;
      });

    const routeDataSubscription = this.route.data.subscribe((data) => {
      this.authType = data.action;
    });

    this.subs.add(routeStateSubscription);
    this.subs.add(routeDataSubscription);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  async login(event: Event) {
    event.preventDefault();
    this.isLoading = true;
    try {
      const deviceToken = await this.auth.requestDevice(this.phone, TOKEN_TYPE.SMS);
      this.tokenService.setDeviceToken(deviceToken.signature, this.password);

      await this.auth.login(this.phone, this.password);

      this.router.navigateByUrl('/');
    } catch (error) {
      this.router.navigate(['/auth/login'], {
        state: {
          error: error.description || error,
        },
      });
    } finally {
      this.isLoading = false;
    }
  }
}
