import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Subscription } from 'rxjs';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { NavService } from '@shared/nav/nav.service';

import { IIN_STATUS, AUTH_TYPE } from '@auth/auth.model';
import { CanRestoreComponent, CanRegisterComponent } from '@auth/modals';
import { IAuthIinResult } from '@auth/helpers/auth-iin/auth-iin.component';
import { CompanyService } from '@company/company.service';

const MODAL_OPTIONS = {
  backdropClass: 'backdrop_light',
  windowClass: 'modal_light modal-auth',
  size: 'md',
  centered: true,
};

@Component({
  selector: 'j-register-iin',
  templateUrl: './register-iin.component.html',
  styleUrls: ['./register-iin.component.scss'],
})
export class RegisterIinComponent implements OnDestroy {
  title: string;
  authType: AUTH_TYPE;

  subs = new Subscription();
  isFromAtf = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private navService: NavService,
    private modalService: NgbModal,
    private companyService: CompanyService
  ) {
    this.navService.triggerActiveItem(route.snapshot.url[0].path);
    this.navService.triggerRegisterError('iin', false);
    this.navService.triggerRegisterError('no-account', false);
    this.navService.triggerRegisterError('no-email', false);
    this.navService.triggerRegisterError('user-blocked', false);
    this.navService.triggerRegisterError('phone-not-match', false);

    const routeDataSubscription = this.route.data.subscribe((data) => {
      this.title = data.title;
      this.authType = data.action;
    });

    const routeParamSubscription = this.route.queryParams.subscribe(
      (queryParams) => {
        this.isFromAtf = this.companyService.isFromAtf(queryParams);
      }
    );

    this.subs.add(routeDataSubscription);
    this.subs.add(routeParamSubscription);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  onSubmit(result: IAuthIinResult) {
    this.navigateToPhone(result.iin);
    // this.handleCheckIin(result.status, result.iin);
  }

  private async handleCheckIin(iinStatus: IIN_STATUS, iin: string) {
    switch (iinStatus) {
      case IIN_STATUS.BLOCKED:
        this.navigateToUserBlocked();
        break;
      case IIN_STATUS.DOES_NOT_EXIST:
        this.navigateToNoAccount();
        break;
      case IIN_STATUS.INCORRECT_DATA:
      case IIN_STATUS.INVALID_DATA:
        this.navigateToNoEmail();
        break;
      case IIN_STATUS.ALREADY_REGISTERED:
        this.handleAlreadyRegistered(iin);
        break;
      case IIN_STATUS.CAN_REGISTER:
        this.handleCanRegister(iin);
        break;
    }
  }

  private handleAlreadyRegistered(iin: string) {
    switch (this.authType) {
      case AUTH_TYPE.RESTORE:
        this.navigateToPhone(iin);
        break;
      case AUTH_TYPE.REGISTER:
        this.showCanRestoreModal(iin);
        break;
    }
  }

  private async showCanRegisterModal(iin: string) {
    const modalRef = this.modalService.open(
      CanRegisterComponent,
      MODAL_OPTIONS
    );
    try {
      await modalRef.result;
      this.navigateToRegisterPhone(iin);
    } catch (error) {}
  }

  private async showCanRestoreModal(iin: string) {
    const modalRef = this.modalService.open(CanRestoreComponent, MODAL_OPTIONS);
    try {
      const closeAction = await modalRef.result;
      if (closeAction == 'restore') {
        this.navigateToRestorePhone(iin);
      } else if (closeAction == 'login') {
        this.navigateToLogin();
      }
    } catch (error) {}
  }

  private handleCanRegister(iin: string) {
    switch (this.authType) {
      case AUTH_TYPE.REGISTER:
        this.navigateToPhone(iin);
        break;
      case AUTH_TYPE.RESTORE:
        this.showCanRegisterModal(iin);
        break;
    }
  }

  private navigateToPhone(iin: string, prefix: string = '') {
    this.router.navigate([`${prefix}phone`], {
      relativeTo: this.route.parent,
      state: {
        iin,
      },
    });
  }

  private navigateToRegisterPhone(iin: string) {
    this.navigateToPhone(iin, '../register/');
  }

  private navigateToRestorePhone(iin: string) {
    this.navigateToPhone(iin, '../restore/');
  }

  private navigateToLogin() {
    this.router.navigate([`../login`], {
      relativeTo: this.route.parent,
    });
  }

  private navigateToNoAccount() {
    this.router.navigate([`no-account`], {
      relativeTo: this.route.parent,
    });
  }

  private navigateToNoEmail() {
    this.router.navigate([`no-email`], {
      relativeTo: this.route.parent,
    });
  }
  private navigateToUserBlocked() {
    this.router.navigate([`user-blocked`], {
      relativeTo: this.route.parent,
    });
  }
}
