import { Component } from '@angular/core';
import { NavService } from '@shared/nav/nav.service';

@Component({
  selector: 'j-no-email',
  templateUrl: './no-email.component.html',
  styleUrls: ['./no-email.component.scss'],
})
export class NoEmailComponent {
  constructor(private navService: NavService) {
    this.navService.triggerRegisterError('iin', true);
    this.navService.triggerRegisterError('no-email', true);
  }
}
