import {
  Component,
  ViewChild,
  Output,
  EventEmitter,
  Input,
  OnDestroy,
  OnChanges,
  SimpleChanges,
  AfterViewInit,
  OnInit,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { Subscription } from 'rxjs';

import { NgbTooltip } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';

import { CompanyService } from '@company/company.service';

import { PasswordInputComponent } from '@shared/input/password-input/password-input.component';
import { PhoneInputComponent } from '@shared/input/phone-input/phone-input.component';
import { MaskedService } from '@shared/masked/masked.service';
import { DictionariesService } from '@shared/services';
import { ICountryCode, ICountryCodeResponseObj } from '@shared/models';

import { DEFAULT_COUNTRY, DEFAULT_COUNTRY_CODE } from '@auth/auth.model';

export interface ILoginForm {
  phone: string;
  password: string;
  countryCode: ICountryCode;
}

@Component({
  selector: 'j-login-phone',
  templateUrl: './login-phone.component.html',
  styleUrls: ['./login-phone.component.scss'],
})
export class LoginPhoneComponent
  implements OnInit, OnDestroy, OnChanges, AfterViewInit
{
  @Input()
  isLoading: boolean;

  isSubmitted = false;
  isPhoneSubmitted = false;

  @Input()
  phone: string;

  @Input()
  passwordError: string;

  @Input()
  loginError: string;

  @Input()
  prefix: string;

  @ViewChild('phoneInput') phoneInput: PhoneInputComponent;
  @ViewChild('passwordInput') passwordInput: PasswordInputComponent;

  @ViewChild('registerButton') registerButton: NgbTooltip;

  loginForm: FormGroup;

  PHONE = localStorage.getItem('phone') ? localStorage.getItem('phone') : '';
  PASSWORD = '';
  countryCodes: ICountryCode[];

  passwordRegExp = /[^ ]/;
  passwordPasteRegExp = /[ ]/;
  countryCode = {
    code: this.PHONE || '+7',
    country: this.translateService.instant(
      `COUNTRIES.${DEFAULT_COUNTRY.KAZAKHSTAN}`
    ),
    id: DEFAULT_COUNTRY.KAZAKHSTAN,
    countryCode: DEFAULT_COUNTRY_CODE.KAZAKHSTAN,
  };

  @Output() submitted = new EventEmitter<ILoginForm>();

  readonly subscription = new Subscription();
  queryParams: any;

  isFromAtf = false;

  constructor(
    private maskedService: MaskedService,
    private route: ActivatedRoute,
    private companyService: CompanyService,
    private translateService: TranslateService,
    private dictionariesService: DictionariesService
  ) {
    const routeSubscription = this.route.queryParams.subscribe(
    (queryParams) => {
      this.queryParams = queryParams;
      this.isFromAtf = this.companyService.isFromAtf(queryParams);
    });
    
    this.loginForm = new FormGroup({
      prefix: new FormControl(this.countryCode.code, Validators.required),
      phone: new FormControl(this.PHONE, Validators.required),
      password: new FormControl(this.PASSWORD, Validators.required),
    });
      
    const formSubscription = this.loginForm.valueChanges.subscribe(() => {
      this.isSubmitted = false;
      this.isPhoneSubmitted = false;
      // this.passwordError = '';
      // this.loginError = '';
    });

    this.subscription.add(routeSubscription);
    this.subscription.add(formSubscription);
  }

  ngOnInit() {
    this.fetchCountryCodes();
  }

  async fetchCountryCodes() {
    try {
      await this.dictionariesService
        .fetchCountryCodesDictionary()
        .then((response) => {
          this.countryCodes = response.map(
            (countryObj: ICountryCodeResponseObj) => ({
              id: countryObj.id,
              code: countryObj.value.description,
              country: countryObj.value.name,
              countryCode: countryObj.value.code,
            })
          );
        });
    } catch (err) {}
  }

  public triggerErrorAnimation() {
    this.passwordInput.triggerHelperAnimation();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  ngAfterViewInit() {
    if (this.loginForm.controls.phone.valid) {
      this.passwordInput.focus();
    } else {
      this.phoneInput.focus();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.passwordError?.currentValue) {
      this.passwordError = changes.passwordError.currentValue;
    }
  }

  onTabPress(event: KeyboardEvent) {
    event.preventDefault();

    this.isPhoneSubmitted = true;

    if (this.f.phone.valid) {
      this.passwordInput.focus();
    }
  }

  async onSubmit() {
    this.isSubmitted = true;
    if (this.loginForm.invalid) {
      if (this.loginForm.controls.phone.errors) {
        this.loginError = 'ERRORS.NOT_ENOUGH_SYMBOLS';
      } else if (this.loginForm.controls.password.errors) {
        this.passwordError = 'ERRORS.ENTER_PASSWORD';
      }
      return;
    }

    const phone = this.maskedService.getUnmaskedPhone(this.f.phone.value);
    const password = this.f.password.value;
    const countryCode = this.countryCode;
    this.submitted.emit({
      phone,
      password,
      countryCode,
    });
  }

  get f() {
    return this.loginForm.controls;
  }

  onCountrySelected(countryCode) {
    this.countryCode = countryCode;
  }
}
